body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;
  --font-raleway: Raleway;
  --font-spartan: Spartan;
  --font-alfa-slab-one: "Alfa Slab One";

  /* font sizes */
  --font-size-5xl: 24px;
  --font-size-13xl: 32px;
  --font-size-29xl: 48px;
  --font-size-21xl: 40px;
  --font-size-sm: 14px;
  --font-size-smi: 13px;
  --font-size-base: 16px;
  --font-size-11xl: 30px;
  --font-size-41xl: 60px;
  --font-size-2xs: 11px;
  --font-size-xl: 20px;
  --font-size-xs: 12px;
  --font-size-17xl: 36px;

  /* Colors */
  --color-white: #fff;
  --color-whitesmoke-100: #f5f5f5;
  --color-whitesmoke-200: #f4f4f4;
  --color-black: #000;
  --color-darkgoldenrod-100: #d1a648;
  --color-darkgoldenrod-200: #c9a03c;
  --color-goldenrod: #efb933;
  --color-gray: rgba(0, 0, 0, 0.95);

  /* Gaps */
  --gap-5xl: 24px;

  /* Paddings */
  --padding-xs: 12px;
  --padding-16xl: 35px;
  --padding-6xs: 7px;
  --padding-3xs: 10px;

  /* border radiuses */
  --br-12xl: 31px;
  --br-mini: 15px;
  --br-lg: 18px;
}
